import {
  Roles,
  BaseProps,
  Payload,
  Listener,
  Response,
  EventType,
  ResponseCode,
  IPostMessageBase,
} from './types';
import {
  INIT_CHANNEL,
  TAG,
  INIT_TARGET_ORIGIN,
  DEFAULT_TIMEOUT
} from './constants';

export default abstract class PostMessageBase implements IPostMessageBase {
  private _targetOrigin: string = INIT_TARGET_ORIGIN;
  private _timeout: number = DEFAULT_TIMEOUT;
  private _handleMaps: Map<string, Listener> = new Map();
  private _eventMaps: Map<string, Listener> = new Map();
  private _role: Roles = Roles.HOST;

  constructor(public options: BaseProps, role: Roles) {
    window.addEventListener('message', this._receiveMessage);
    this._role = role;

    const { targetOrigin, timeout } = options;

    if (targetOrigin) {
      this._targetOrigin = targetOrigin;
      this.handle(INIT_CHANNEL, () => window.location.origin);
    } else {
      this._getTargetOrigin();
    }

    if (timeout) this._timeout = timeout;
  }

  private _getTargetOrigin = async () => {
    try {
      const result = await this.invoke(INIT_CHANNEL);
      const { payload } = result;
      payload && (this._targetOrigin = payload);
      this._debug(`_targetOrigin: ${payload}`);
    } catch (e) {}
  }

  private _receiveMessage = async (messageEvent: MessageEvent) => {
    const { data, ports, origin } = messageEvent;

    const { payload, type, channel } = data;
    this._debug(`_receiveMessage type: ${type}, channel: ${channel}, payload: ${JSON.stringify(payload)}`);
    if (!type || ![EventType.EVENT, EventType.HANDLE].includes(type)) return;

    if (type === EventType.EVENT) {
      const func = this._eventMaps.get(channel);
      if (!func || typeof func !== 'function') return;
      this._debug(`_receiveMessage type: ${EventType.EVENT}`);
      func(payload, messageEvent);
      return;
    }

    const port = ports[0];

    this._debug(`_receiveMessage origin: ${origin}, targetOrigin: ${this.targetOrigin}`);
    if (origin && this._targetOrigin !== '*' && origin !== this.targetOrigin) {
      return;
    };

    const func = this._handleMaps.get(channel);
    if (!func || typeof func !== 'function') return;

    let result: any = {}
    try {
      result.payload = await func(payload, messageEvent);
      result.code = ResponseCode.SUCCESS;
    } catch (e) {
      result.code = ResponseCode.ERROR;
      result.error = this._formatMsg(`${channel} ${e.message}`);
    }

    this._debug(`_receiveMessage result: ${JSON.stringify(result)}`);

    port.postMessage(result);
  }

  private _formatMsg = (msg: string) => {
    return `[${TAG}-${this._role}]: ${msg}`;
  }

  private _wait = (channel: string, msgChannel: MessageChannel, timeout?: number,) => {
    const time = timeout || this._timeout;
    const timeoutResponse = {
      code: ResponseCode.TIMEOUT,
      error: this._formatMsg(`${channel} is timeout.`),
      payload: null,
    };
    return new Promise<Response>((resolve, reject) => setTimeout(() => {
      msgChannel.port1.close();
      reject(timeoutResponse);
    }, time));
  }

  private _debug = (msg: string) => {
    this.options.debug && console.info(this._formatMsg(msg));
  }

  public invoke = (channel: string, payload?: Payload, timeout?: number) => {
    const { contentWindow } = this.options;
    if (!contentWindow) {
      return Promise.reject({
        code: ResponseCode.ERROR,
        error: this._formatMsg(`${channel} contentWindow is undefined`),
      });
    }
    const msgChannel = new MessageChannel();
    this._debug(`invoke ${channel}`);

    return Promise.race([
      new Promise<Response>((resolve, reject) => {
        contentWindow.postMessage({
          type: EventType.HANDLE,
          channel,
          payload
        }, this._targetOrigin, [msgChannel.port2]);

        msgChannel.port1.onmessage = (messageEvent: MessageEvent) => {
          const result = messageEvent.data as Response;
          if (result.code !== ResponseCode.SUCCESS) {
            reject(result);
            return;
          }
          resolve(result);
          msgChannel.port1.close();
        }

        msgChannel.port1.onmessageerror = (messageEvent: MessageEvent) => {
          const error = {
            code: ResponseCode.ERROR,
            error: this._formatMsg(`${channel} onmessageerror`),
          };
          reject({ ...messageEvent.data, ...error });
          msgChannel.port1.close();
        }
      }),
      this._wait(channel, msgChannel, timeout),
    ])
  }

  public handle = (channel: string, listener: Listener) => {
    this._debug(`handle ${channel}`);
    this._handleMaps.set(channel, listener);
  }

  public handleOnce = (channel: string, listener: Listener) => {
    this._handleMaps.set(channel, (payload: Payload, event: MessageEvent) => {
      this.removeHandler(channel);
      return listener(payload, event);
    });
  }

  public removeHandler = (channel: string) => {
    this._handleMaps.delete(channel);
  }

  public removeAllHandler = () => {
    this._handleMaps.clear();
  }

  public send = (channel: string, payload: Payload) => {
    const { contentWindow } = this.options;
    if (!contentWindow) return;

    this._debug(`send ${channel} ${this._targetOrigin}`);
    contentWindow.postMessage({
      type: EventType.EVENT,
      channel,
      payload
    }, this._targetOrigin);
  }

  public on = (channel: string, listener: Listener) => {
    this._debug(`on ${channel}`);
    this._eventMaps.set(channel, listener);
  }

  public once = (channel: string, listener: Listener) => {
    this._eventMaps.set(channel, (payload: Payload, event: MessageEvent) => {
      this.removeListener(channel);
      return listener(payload, event);
    });
  }

  public removeListener = (channel: string) => {
    this._eventMaps.delete(channel);
  }

  public removeAllListeners = () => {
    this._eventMaps.clear();
  }

  public get targetOrigin () {
    return this._targetOrigin;
  }

  public get role () {
    return this._role;
  }

  public dispose = () => {
    window.removeEventListener('message', this._receiveMessage);
    this.removeAllListeners();
    this.removeAllHandler();
  }
};

