import { IPostMessageManager, HostProps, Payload } from './types';
import uuid from './utils';
import PostMessageHost from './PostMessageHost';
import { TAG } from './constants';

export default class PostMessageManager implements IPostMessageManager {
  private static _postMessageHostMaps = new Map<string, PostMessageHost>();

  static addPostMessageHost = (id: string, instance: PostMessageHost) => {
    PostMessageManager._postMessageHostMaps.set(id, instance);
  }

  public create = (options: HostProps) => {
    const errorTag = `[${TAG}-Host]:`;
    if (!options.contentWindow) {
      throw new Error(`${errorTag} Missing required parameters contentWindow`);
    }
    if (!options.targetOrigin) {
      throw new Error(`${errorTag} Missing required parameters targetOrigin`);
    }
    const id = uuid();
    const instance = new PostMessageHost(options, id);
    PostMessageManager.addPostMessageHost(id, instance);

    return instance;
  }

  public destroy = (id: string) => {
    PostMessageManager._postMessageHostMaps.delete(id);
  }

  public broadcast = (channel: string, payload: Payload) => {
    for (const instance of PostMessageManager._postMessageHostMaps.values()) {
      // @ts-ignore
      if (instance && instance instanceof PostMessageHost) {
        instance.send(channel, payload);
      }
    }
  }
}
